import jsfunPicker from '@/components/jsfun-picker/jsfun-picker.vue'
import chooseImage from '@/components/xyz-choose-image/xyz-choose-image.vue';
import xflSelect from '@/components/xfl-select/xfl-select.vue'; 
import lotusAddress from "../../components/Winglau14-lotusAddress/Winglau14-lotusAddress.vue";
	export default {
		components: {
			chooseImage,
			xflSelect,
			jsfunPicker,
			"lotus-address":lotusAddress,
		},
		data() {
			return {
				ApartmentList: ['一套一', '一套一', '一套一', '35.00','40.00', '45.00', '50.00', '60.00'],
				RenovationList: ['金装', '25.00', '30.00', '35.00','40.00', '45.00', '50.00', '60.00'],
				OrientationList: ['南', '25.00', '30.00', '35.00','40.00', '45.00', '50.00', '60.00'],
		
				
				Default:'请选择',     
				lotusAddressData:{
				                visible:false,
				                provinceName:'',
				                cityName:'',
				                townName:'',
				            },
				            region:'',
				tabActiveIdx:0,
				hasChooseImg: '',
				  list: [       //要展示的数据
				                '苹果',
				                {value: '香蕉', disabled: true},
				                '葡萄',
				                '芒果',
				                '大白菜',
				            ]
				
			};
		},
		onLoad() {
			uni.getSystemInfo({
				success(res) {
					console.log(res)
				}
			})
		},
		methods: {
			 priceChange(data){
			        console.log("==返回值==");
			        console.log(data);
			        console.log(data.indexStr);
			        console.log(data.textStr);
			    },
			chooseImage(imgArr) {
				console.log(imgArr);
				this.hasChooseImg = imgArr;
			},
			savePhoto(path) {},
			tabSelect(idx) {
				this.tabActiveIdx = idx
			},
			add(){
				var that = this;
				uni.request({
					url: 'http://106.13.239.202/index.php?c=sale&m=addsale',
					method: 'POST',
					header: {"content-type": "application/x-www-form-urlencoded"},
					data: {
						name:"王攀",
						time:"12月",
						phone:"15680781369",
						money:"1000",
						type:1,
						area:"100",
						direction:"南",
						door:"11幢120",
						level:"11楼",
						fitment:"精装",
						describle:"超级便宜",
						picture:"dada",
						stute:0
					},
					success: res => {
						if(res.data.status == "OK"){
							console.log("成功")
							uni.showToast({
							    title: '发送给工作人员成功！',
							    duration: 2000,
								icon:"none"
							});
						}
						  
					       
					},
					fail: () => {
					
					},
					complete: () => {
						
					}
				});
			}
		},
		
		
		onReachBottom() {
			this.masonryList = this.masonryList.concat(this.masonryList);
		}
		     
	};
